* {
  font-family: "PT Sans Caption", sans-serif, "arial", "Times New Roman";
}

/* Error Page */

.error {
  margin: 0 auto;
  height: 55vh;
  .clip {
    .shadow {
      height: 180px;

      /*Contrall*/
    }

    &:nth-of-type(2) .shadow {
      width: 130px;

      /*Contrall play with javascript*/
    }

    &:nth-of-type(1) .shadow,
    &:nth-of-type(3) .shadow {
      width: 250px;

      /*Contrall*/
    }
  }

  .digit {
    width: 150px;

    /*Contrall*/
    height: 150px;

    /*Contrall*/
    line-height: 150px;

    /*Contrall*/
    font-size: 120px;
    font-weight: bold;
  }

  h2 {
    font-size: 32px;
  }

  .msg {
    top: -190px;
    left: 30%;
    width: 80px;
    height: 80px;
    line-height: 80px;
    font-size: 32px;
  }

  span.triangle {
    top: 70%;
    right: 0%;
    border-left: 20px solid #535353;
    border-top: 15px solid transparent;
    border-bottom: 15px solid transparent;
  }

  .container-error-404 {
    margin-top: 22%;
    position: relative;
    height: 250px;
    padding-top: 40px;

    .clip {
      display: inline-block;
      transform: skew(-45deg);
    }
  }

  .clip {
    .shadow {
      overflow: hidden;
    }

    &:nth-of-type(2) .shadow {
      overflow: hidden;
      position: relative;
      box-shadow: inset 20px 0px 20px -15px rgba(150, 150, 150, 0.8),
        20px 0px 20px -15px rgba(150, 150, 150, 0.8);
    }

    &:nth-of-type(3) .shadow:after,
    &:nth-of-type(1) .shadow:after {
      content: "";
      position: absolute;
      right: -8px;
      bottom: 0px;
      z-index: 1;
      height: 100%;
      width: 10px;
      background: linear-gradient(
        90deg,
        transparent,
        rgba(173, 173, 173, 0.8),
        transparent
      );
      border-radius: 50%;
    }

    &:nth-of-type(3) .shadow:after {
      left: -8px;
    }
  }

  .digit {
    position: relative;
    top: 8%;
    color: white;
    background: #07b3f9;
    border-radius: 50%;
    display: inline-block;
    transform: skew(45deg);
  }

  .clip {
    &:nth-of-type(2) .digit {
      left: -10%;
    }

    &:nth-of-type(1) .digit {
      right: -20%;
    }

    &:nth-of-type(3) .digit {
      left: -20%;
    }
  }

  h2 {
    color: #a2a2a2;
    font-weight: bold;
    padding-bottom: 20px;
  }

  .msg {
    position: relative;
    z-index: 1;
    display: block;
    background: #535353;
    color: #a2a2a2;
    border-radius: 50%;
    font-style: italic;
  }

  .triangle {
    position: absolute;
    z-index: 1;
    transform: rotate(45deg);
    content: "";
    width: 0;
    height: 0;
  }
}

/* Error Page */
@media (max-width: 767px) {
  /* Error Page */

  .error {
    .clip {
      .shadow {
        height: 100px;

        /*Contrall*/
      }

      &:nth-of-type(2) .shadow {
        width: 80px;

        /*Contrall play with javascript*/
      }

      &:nth-of-type(1) .shadow,
      &:nth-of-type(3) .shadow {
        width: 100px;

        /*Contrall*/
      }
    }

    .digit {
      width: 80px;

      /*Contrall*/
      height: 80px;

      /*Contrall*/
      line-height: 80px;

      /*Contrall*/
      font-size: 52px;
    }

    h2 {
      font-size: 24px;
    }

    .msg {
      top: -110px;
      left: 15%;
      width: 40px;
      height: 40px;
      line-height: 40px;
      font-size: 18px;
    }

    span.triangle {
      top: 70%;
      right: -3%;
      border-left: 10px solid #535353;
      border-top: 8px solid transparent;
      border-bottom: 8px solid transparent;
    }

    .container-error-404 {
      height: 150px;
    }
  }

  /* Error Page */
}

